.bg_hero{
    /* background-color: #1E2022; */
    background-color: var(--bgx);
}

.hero_li{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.hero_li .item{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    list-style: none;
    padding: 15px;
    border-bottom: 1px solid var(--secondary);
    cursor: pointer;
}

.hero_li .item .glass{
    left: 0;
    width: 0%;
    height: 100%;
    position: absolute;
    background-color: var(--primary);
    /* background: linear-gradient(90deg, var(--secondary) 10%, var(--primary) 100%); */
    transition: width 500ms ease;
}
.hero_li .item li{
    position: relative;
    z-index: 1;
}

.hero_li .item:hover .glass{
    width: 100%;
}

.hero_li .item:hover li{
    color: var(--secondary);
}
.ext_tlr{
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;
}
.flex_height{
    height: 87%;
}
.bg_img{
    border-radius: 20px;
    filter: grayscale(2);
}

@media (max-width: 767px) { 
    .hero_li{
        flex-direction: row;
        
    }
    .hero_li .item:last-child{
        display: none;
    }
    .hero_li .item{
        border-bottom: 0;
        border: 0.3px solid var(--secondary);
        width: 100%;
    }
 }